.header-bar h1 {
  font-size: 96px;
  color: white;
  margin: 48px 0;
  text-shadow: 4px 4px 10px rgba(0,0,0, 0.3);
}

.header-bar button {
  max-width: 100px;
}

@media (max-width: 600px) {
  .header-bar h1 {
    font-size: 56px;
    margin: 16px 0;
  }
}
.card {
  margin: 0 auto;
  background: white;
  padding: 20px 30px;
  max-width: 500px;
  width: 500px;
  max-height: 600px;
  height: 600px;
  border-radius: 5px;
  box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.card > :first-child {
  flex-grow: 1;
}

.card > :last-child {
  max-width: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.log-out {
  background: rgb(221,221,221);
  color: black;
}

@media (max-width: 600px) {
  .card {
    max-width: 100%;
    width: 100%;
    height: auto;
  }

  .card form {
    width: 100%;
  }
}
.form-input {
  display: flex;
  flex-direction: column;
}
.login-form {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-evenly;
  max-width: 100%;
  width: 80%;
}

.login-form label {
  font-size: 28px;
}

.login-form button {
  font-weight: bold;
}
.landing-page {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  justify-content: space-between;
}

.landing-page a {
  color: white;
  font-size: 28px;
  text-decoration: none;
}
.question {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 100%;
  width: 100%;
}

.question > .question-text {
  flex-grow: 1;
  font-size: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.question > .question-user-response {
  display: flex;
  flex-direction: column;
  padding: 25px 0;
}

.question > .question-user-response > :first-child {
  margin-bottom: 10px;
}

.question-answer {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 100%;
}

.question-answer > h2 {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 48px;
}

.question-answer > form {
  margin: 30px 0;
}

@media (max-width: 600px) {
  .question-answer > h2, .question-text {
    margin: 24px 0;
  }
}
.registration-page {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  justify-content: space-between;
}

.registration-page a {
  color: white;
  font-size: 28px;
  text-decoration: none;
}

.app {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  align-items: center;
}

@media (max-width: 600px) {
  .app > :last-child {
    flex-grow: 1;
    width: 90%;
    max-width: 90%;
  }
}
* {
  box-sizing: border-box;
}

body {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: #5555d5;
  text-align: center;
  height: 100vh;
  max-height: 100vh;
}

input {
  padding: 0 5px;
}

button:disabled {
  background: lightgray;
  cursor: default;
  color: gray;
}

button {
  background: #5555d5;
  color: white;
  cursor: pointer;
}

input, button {
  height: 50px;
  max-height: 50px;
  width: 100%;
  max-width: 100%;
  border: 1px solid rgba(0,0,0,.3);
  border-radius: 5px;
  font-size: 24px;
}

.form-warning {
  color: yellow;
}

.form-error {
  color: red;
}

#root {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
